'use strict';


var app            = require('mag-app'),
    pkgJson        = require('../../manifest.json'),
    XHRConstructor = null;


app.storage = {};

function catchMessage () {
    var sharedBackend = require('./modules/shared.backend').instance;

    app.stalker.request('message', {
        onload: function ( error, response ) {
            if ( !error && response.data.type ) {
                sharedBackend.emit('message', response.data);

                switch ( response.data.type ) {
                    case 'reboot':
                        if ( PLATFORM === 'MAG' ) {
                            console.log('reboot');

                            window.core.notify({
                                title: 'Reboot',
                                icon: 'info',
                                type: 'success',
                                timeout: 7000
                            });

                            setTimeout(function () {
                                // eslint-disable-next-line new-cap
                                window.top.gSTB.ExecAction('reboot');
                            }, 7000);
                        }
                        break;
                    case 'send_msg':
                        window.core.notify({
                            title: response.data.msg,
                            icon: 'info',
                            type: 'success',
                            timeout: 7000
                        });

                        break;
                    case 'send_msg_with_video':
                        window.core.notify({
                            title: response.data.msg,
                            icon: 'info',
                            type: 'success',
                            buttons: [
                                {
                                    value: gettext('Ok'),
                                    click: function () {
                                        window.core.intent({
                                            action: 'play',
                                            mime: 'content/media',
                                            data: {
                                                //uri: 'udp://@228.1.2.6:1234'
                                                uri: response.data.url
                                            }
                                            // eslint-disable-next-line no-unused-vars
                                        }, function ( error, context ) {
                                            if ( error ) {
                                                console.log(error);
                                            }
                                        });
                                    }
                                }
                            ]
                        });

                        break;
                    default:
                        console.log('catch message with type: ' + response.data.type);
                        break;
                }
            }
        }
    });
}


function ping ( timeout ) {
    setTimeout(function () {
        console.log('PING START, timeout: ' + timeout);
        app.stalker.request('users/' + app.stalker.session.userId + '/ping', {
            onload: function ( error, data ) {
                console.log('PING END');
                if ( !error ) {
                    if ( data ) {
                        if ( data.data && data.data.message_available ) {
                            catchMessage();
                        }
                    }
                }

                ping(timeout);
            }
        });
    }, timeout);
}

window.fetch = function () {
    if ( !XHRConstructor ) {
        XHRConstructor = XMLHttpRequest;
    }

    return new XHRConstructor();
};

