// everything is ready
app.once('load', function () {
    window.core.once('ready', function ( event ) {
        var packages      = {},
            globalOptions = window.core.config.options,
            appsCount     = window.core.config.apps.filter(function ( appConfig ) {
                if ( appConfig.type === 'app' ) {
                    packages[appConfig.packageName] = appConfig;

                    return true;
                }

                return false;
            }).length;


        require('./modules/shared.backend').instance.session = app.stalker.session;

        if ( appsCount === 0 && typeof globalOptions.stalkerLoaderPath !== 'undefined' ) {
            event.stop = true;

            if ( typeof window.core.redirectTo === 'function' ) {
                window.core.redirectTo(globalOptions.stalkerHost + globalOptions.stalkerLoaderPath);
            }

            return;
        }

        // if ( PLATFORM === 'MAG' ) {
        // } else {
        ping(window.core.config.options.pingTimeout);
        // ping(2000);
        // }
    });
    require('spa-gettext').load({name: core.environment.language}, function () {
        var session = {};

        window.addEventListener('online', function () {
            window.core.notify({
                title: gettext('Network connected'),
                icon: 'info',
                type: 'success',
                timeout: 10000
            });
        });

        window.addEventListener('offline', function () {
            window.core.notify({
                title: gettext('Network disconnected'),
                icon: 'info',
                type: 'success',
                timeout: 10000
            });
        });

        session = core.plugins.ufsJson.loadSync();
        console.log(JSON.stringify(session));

        require('./modules/stalker.api.loader')({
            apiVersion: pkgJson.config.apiVersion,
            options: window.core.config.options,
            session: session
        });
    });
});
