//function declaration
function initCreateForm ( data, update ) {
    inputLogin.setValue(data.login);
    inputLogin.$node.classList.remove('error');
    inputPassword.setValue(data.password);
    inputPassword.$node.classList.remove('error');
    inputServerAddress.setValue(data.address);
    inputServerAddress.$node.classList.remove('error');
    inputActivateCode.setValue(data.activationCode);
    inputActivateCode.$node.classList.remove('error');

    if ( update ) {
        buttonSubmit.updateId = accountManager.get().indexOf(data);
        buttonSubmit.$text.innerHTML = gettext('Save');
        buttonSubmit.state = 'update';
    }
}


function updateAccountsCounter ( index ) {
    var length     = accountList.data.length,
        focusIndex = typeof index === 'undefined' ? (accountList.data.indexOf(accountList.$focusItem.data) + 1) : index + 1;

    window.pmlAccountListButtonScrollUp.innerText = focusIndex + '/' + length;
}


function loadTheme ( callback ) {
    var xhr = new XMLHttpRequest();

    xhr.open('GET', app.stalker.host + '/stalker_portal/server/api/launcher_profile.php?language=en&uid=' + app.stalker.session.userId);
    xhr.onload = function () {
        var config = null;

        console.log('config response text');

        try {
            config = JSON.parse(this.responseText);
        } catch ( error ) {
            console.log(error);
        }

        if ( config === null ) {
            // window.top.console.log('config is null');
            callback();
        } else if ( config.options && config.options.themePath ) {
            config.theme = {
                path: app.stalker.host + '/stalker_portal/server/api/theme_css.php?uid=' +
                app.stalker.session.userId + '&_=' + Date.now() + '&resolution=',
                vars: null
            };
            // xhr = new XMLHttpRequest();
            xhr.open('GET', config.theme.path + window.screen.height);

            xhr.onload = function () {
                window.top.console.log('onload theme ' + this.status);

                if ( this.status === 200 ) {
                    parent.extendCorePrototype('theme', config.theme);
                }
                callback();
            };

            xhr.onerror = xhr.ontimeout = function ( ) {
                console.log('onerror timeout ' + this.status);

                callback();
            };

            xhr.send();
        } else {
            console.log('no theme path');
            callback();
        }
    };

    xhr.onerror = xhr.ontimeout = function ( ) {
        // window.top.console.log('theme on error, on timeout');
        callback();
    };

    xhr.send();
}


function loadSettings ( globalOptions, callback ) {
    //window.top.sharedBackend = sharedBackend;
    console.log('USERS STORAGE');

    app.stalker.request('storage/' + storageKey, {
        onload: function ( error, data ) {
            console.log('APP STORAGE LAODED ' + JSON.stringify(data));

            if ( error ) {
                app.stalker.request('storage/', {
                    method: 'POST',
                    body: JSON.stringify({key: storageKey, value: '{}'})
                });
                window.storage = {};
            } else {
                window.storage = JSON.parse(data.data.value);
            }

            app.stalker.request('users/' + app.stalker.session.userId + '/settings', {
                onload: function ( error, settingsResponse ) {
                    var packages      = {},
                        sharedBackend = require('../modules/shared.backend');


                    core.config.apps.forEach(function ( appConfig ) {
                        if ( appConfig.type === 'app' ) {
                            packages[appConfig.packageName] = appConfig;

                            return true;
                        }

                        return false;
                    });

                    if ( !error && settingsResponse ) {
                        if ( PLATFORM === 'MAG' ) {
                            if ( settingsResponse.data.show_after_loading === 'last_channel' ) {
                                if ( packages['magcore-app-tv'] ) {
                                    core.taskManager.setHomeApp(packages['magcore-app-tv']);
                                }
                            }
                        }

                        app.parentPassword = settingsResponse.data.parent_password;
                        /* eslint max-len: 0 */
                        core.environment.audioPrimaryLanguage = settingsResponse.data.pri_audio_lang || settingsResponse.data.languages[0];
                        core.environment.audioSecondaryLanguage = settingsResponse.data.sec_audio_lang || settingsResponse.data.languages[1];
                        core.environment.subtitlesPrimaryLanguage = settingsResponse.data.pri_subtitle_lang || settingsResponse.data.languages[0];
                        core.environment.subtitlesSecondaryLanguage = settingsResponse.data.sec_subtitle_lang || settingsResponse.data.languages[1];

                        sharedBackend.instance.settings = settingsResponse.data;
                        parent.extendCorePrototype('environment', core.environment);

                        require('../modules/shared.backend').instance.emit('ready');
                    }
                    callback();
                },
                onerror: function ()  {
                    callback(true);
                },
                ontimeout: function ()  {
                    callback(true);
                }
            });
        },
        onerror: function ()  {
            callback(true);
        },
        ontimeout: function ()  {
            callback(true);
        }
    });

    parent.extendCorePrototype('Storage', require('../modules/storage'));
}


function loadModules ( callback ) {
    app.stalker.request('modules', {
        onload: function ( error, data ) {
            var map = {
                    // media_browser: 'media_browser',
                    'magcore-app-tv': 'tv',
                    // apps: 'apps',
                    // dvb: 'dvb',
                    // tv_archive: 'tv_archive',
                    // time_shift: 'time_shift',
                    // time_shift_local: 'time_shift_local',
                    // 'epg.reminder': 'epg.reminder',
                    // 'epg.recorder': 'epg.recorder',
                    // 'epg': 'epg',
                    // 'epg.simple': 'epg.simple',
                    'magcore-app-video-club': 'vclub',
                    'magcore-app-audio-club': 'audioclub',
                    'magcore-app-magic-cast': 'magiccast',
                    // 'downloads_dialog': 'downloads_dialog',
                    // 'downloads': 'downloads',
                    // 'karaoke': 'karaoke',
                    'magcore-app-radio': 'radio',
                    // 'weather.current': 'weather.current',
                    // 'widget.audio': 'widget.audio',
                    // 'widget.radio': 'widget.radio',
                    // 'records': 'records',
                    // 'remotepvr': 'remotepvr',
                    // 'pvr_local': 'pvr_local',
                    'magcore-app-settings': 'settings',
                    // 'course.nbu': 'course.nbu',
                    // 'weather.day': 'weather.day',
                    // 'cityinfo': 'cityinfo',
                    // 'horoscope': 'horoscope',
                    // 'anecdote': 'anecdote',
                    // 'game.mastermind': 'game.mastermind',
                    'magcore-app-personal-account': 'account'
                    // 'demo': 'demo',
                    // 'infoportal': 'infoportal',
                    // 'internet': 'internet',
                    // 'service_management': 'service_management',
                    // 'logout': 'logout',
                    // 'account_menu': 'account_menu'
                },
                originPackages   = core.packages,
                resolvedPackages = [];

            if ( !error && data ) {
                originPackages.forEach(function ( item ) {
                    var originName;

                    if ( item.type === 'app' ) {
                        originName = item.packageName.split('-');
                        // remove platform postfix
                        originName.pop();
                        originName = originName.join('-');
                        if ( data.data.indexOf(map[originName]) !== -1 ) {
                            resolvedPackages.push(item);
                        }
                    } else {
                        resolvedPackages.push(item);
                    }
                });
                core.updatePackages(resolvedPackages);
            }

            console.log('modules loaded and parsed');

            loadSettings(core.config.options, callback);
        },
        onerror: function ()  {
            console.log('loadModules onerror ');

            callback(true);
        },
        ontimeout: function ()  {
            console.log('loadModules ontimeout ');

            callback(true);
        }
    });
}


function login ( credentials, callback ) {
    window.pmlLabelError.innerText = '';
    window.pmlLabelError.className = '';
    inputLogin.$node.classList.remove('error');
    inputPassword.$node.classList.remove('error');
    inputServerAddress.$node.classList.remove('error');
    inputActivateCode.$node.classList.remove('error');
    app.stalker.auth.login(credentials, function ( error, response ) {
        window.top.console.log('login error: ' + error);
        if ( error ) {
            callback(error, response);
        } else {
            if ( requestCallback ) {
                requestCallback();
            }
            requestCallback = null;

            loadTheme(function ( error ) {
                if ( error ) {
                    callback(error);

                    return;
                }

                loadModules(function ( error ) {
                    if ( error ) {
                        callback(error);

                        return;
                    }

                    core.call('hide');
                    core.call('blur');

                    core.events['show'] = null;
                    core.emit('ready', {stop: false});
                    callback(null, response);
                });
            });
        }
    });
}


function preventedKeydown ( event ) {
    event.preventDefault();
}


function showApp ( callback ) {
    requestCallback = callback;
    core.emit('show');
    core.call('show');
    core.call('focus');
}


function errorNotification ( input ) {
    window.pmlLabelError.innerText = gettext('Error.') + ' ' + errorData[input.$node.id].text;
    window.pmlLabelError.className = 'visible ' + errorData[input.$node.id].className;
    input.focus();
    input.$node.classList.add('error');
    buttonSubmit.locked = false;
}


function setErrorPosition ( position ) {
    switch ( position ) {
        case positions.login:
            window.pmlLabelError.className = 'visible';
            inputLogin.$node.classList.add('error');
            inputPassword.$node.classList.add('error');
            inputLogin.focus();
            break;

        case positions.password:
            window.pmlLabelError.className = 'visible passwordError';
            inputLogin.$node.classList.add('error');
            inputPassword.$node.classList.add('error');
            inputLogin.focus();
            break;

        case positions.serverAddress:
            window.pmlLabelError.className = 'visible addressError';
            inputActivateCode.focus();
            inputActivateCode.$node.classList.add('error');
            break;

        case positions.licenseKey:
            window.pmlLabelError.className = 'visible licenseError';
            inputActivateCode.focus();
            inputActivateCode.$node.classList.add('error');
            break;

        case positions.loginButton:
            window.pmlLabelError.className = 'visible unknownError';
            break;
    }
}


function errorHandler ( code ) {
    switch ( code ) {
        case 'Username or password is incorrect':
            window.pmlLabelError.innerText = gettext('Incorrect login or password. Please try again');
            // inputLogin.focus();
            // inputLogin.$node.classList.add('error');
            // inputPassword.$node.classList.add('error');
            setErrorPosition(positions.login);
            break;

        case 'Activation code not found':
        case 'License key not found':
            window.pmlLabelError.innerText = gettext('License key not found');
            setErrorPosition(positions.licenseKey);
            break;

        case 'Operator ID mismatch':
            window.pmlLabelError.innerText = gettext('The License Key doesn\'t work for this portal. Please contact your Key provider.');
            setErrorPosition(positions.licenseKey);
            break;

        case 'Incorrect code. Please try again.':
        case 'Incorrect key. Please try again.':
            window.pmlLabelError.innerText = gettext('Incorrect key. Please try again.');
            setErrorPosition(positions.licenseKey);
            break;

        case 'Activation code missing':
        case 'License key missing':
            window.pmlLabelError.innerText = gettext('License key missing');
            setErrorPosition(positions.licenseKey);

            // window.pmlLabelError.className = 'visible licenseError';
            // inputActivateCode.focus();
            // inputActivateCode.$node.classList.add('error');
            break;

        case 'Meta data mismatch':
            window.pmlLabelError.innerText = gettext('The code youve entered has already been ' +
                'activated on another device. Please contact the code provider.');

            setErrorPosition(positions.licenseKey);
            break;

        case 'Reached limit the number of codes for user.':
            window.pmlLabelError.innerText = gettext('Reached limit the number of codes for user.');
            setErrorPosition(positions.licenseKey);
            break;

        case 'Certificate server error - unable to create certificate':
        case 'Unable to decode certificate server response':
            window.pmlLabelError.innerText = gettext('The license key has not been verified. Please contact your service provider.');
            setErrorPosition(positions.licenseKey);
            break;

        case 'Automatic activation code issuing disabled for user.':
            window.pmlLabelError.innerText = gettext('The license key has not been issued. Please contact your service provider.');
            setErrorPosition(positions.licenseKey);
            break;

        default:
            window.pmlLabelError.innerText = code || gettext('Error');
            setErrorPosition(positions.loginButton);
            break;
    }
}


function errorNetworkNotification ( interval ) {
    clearInterval(interval);
    window.pmlLabelError.innerText = gettext('No network connection');
    window.pmlLabelError.className = 'visible unknownError';
    buttonSubmit.locked = false;
}


