accountsData = accountManager.get();

accountList = new List({
    $node: window.pmlAccountList,
    type: List.prototype.TYPE_VERTICAL,
    size: accountsData.length < 3 ? accountsData.length : 3,
    focusIndex: 0,
    render: function ( $node, data ) {
        if ( !$node.ready ) {
            $node.$buttons = [];
            $node.$buttons.push($node.appendChild(document.createElement('div')));
            $node.$buttons[0].className = 'button account';

            $node.$nameContainer = $node.$buttons[0].appendChild(document.createElement('div'));
            $node.$nameContainer.className = 'name-container';

            $node.$nameContainer.appendChild(document.createElement('div'));
            $node.$nameContainer.firstChild.className = 'label';
            $node.$nameContainer.firstChild.innerText = gettext('Login');
            $node.$login = $node.$nameContainer.appendChild(document.createElement('div'));
            $node.$login.className = 'account';

            $node.$buttons[0].appendChild(document.createElement('div'));
            $node.$buttons[0].lastChild.className = 'theme-icon theme-icon-more';

            $node.$buttons.push($node.appendChild(document.createElement('div')));
            $node.lastChild.className = 'button edit';

            $node.$buttons.push($node.appendChild(document.createElement('div')));
            $node.lastChild.className = 'button remove';
            $node.ready = true;
        }

        $node.$login.innerText = data.login;
        $node.buttonFocusIndex = 0;
    },
    events: {
        overflow: function () {
            //if ( event.direction === keys.down ) {}
        },
        keydown: function ( event ) {
            var $node = this.$focusItem,
                index;

            switch ( event.code ) {
                case keys.right:
                    if ( $node.buttonFocusIndex < 2 ) {
                        $node.$buttons[$node.buttonFocusIndex].classList.remove('focus');
                        ++$node.buttonFocusIndex;
                        $node.$buttons[$node.buttonFocusIndex].classList.add('focus');
                    }
                    break;
                case keys.left:
                    if ( $node.buttonFocusIndex > 0 ) {
                        $node.$buttons[$node.buttonFocusIndex].classList.remove('focus');
                        --$node.buttonFocusIndex;
                        $node.$buttons[$node.buttonFocusIndex].classList.add('focus');
                    }
                    break;

                case keys.back:
                    window.top.platformApi.exit();
                    break;

                case keys.pageUp:
                case keys.pageDown:
                case keys.home:
                case keys.end:
                    // cursor move only on arrow keys
                    if ( PLATFORM === 'SMARTTV' ) {
                        document.body.style.display = 'none';
                        setTimeout(function () {
                            document.body.style.display = '';
                        }, 0);
                    }
                    this.move(event.code);
                    break;
                case keys.ok:
                    this.emit('click:item', {$item: $node, event: event});
                    break;

                case keys.up:
                    setTimeout(function () {
                        accountList.$node.style.opacity = '';
                    }, 0);
                    accountList.$node.style.opacity = '0.5';
                    index = this.data.indexOf($node.data);
                    if ( index > 0 ) {
                        if ( this.$focusItem === this.$body.firstChild && accountList.data.length > 3 ) {
                            buttonScrollUp.focus();
                            console.log('focus up button');
                        } else {
                            this.move(event.code);
                            updateAccountsCounter(index - 1);
                        }
                    }
                    break;

                case keys.down:
                    setTimeout(function () {
                        accountList.$node.style.opacity = '';
                    }, 0);
                    accountList.$node.style.opacity = '0.5';
                    index = this.data.indexOf($node.data);
                    if ( (index + 1) < this.data.length ) {
                        if ( index > 1 && accountList.data.length > 3 ) {
                            buttonScrollDown.focus();
                            console.log('focus down button');
                        } else {
                            updateAccountsCounter(index + 1);
                            this.move(event.code);
                        }
                    } else {
                        buttonNewAccount.focus();
                    }
            }
        },
        'focus:item': function ( event ) {
            console.log('focus item');
            if ( event.$prev ) {
                event.$prev.$buttons[1].style.display = 'none';
                event.$prev.$buttons[2].style.display = 'none';
                event.$prev.$buttons[event.$prev.buttonFocusIndex].classList.remove('focus');
            }

            event.$curr.buttonFocusIndex = 0;
            event.$curr.$buttons[event.$curr.buttonFocusIndex].classList.add('focus');
        },
        'click:item': function ( event ) {
            var self = this,
                focusIndex, viewIndex;

            if ( event.event && event.event.srcElement ) {
                // mouse click
                if ( event.event.srcElement === event.$item.$buttons[1] ) {
                    event.$item.buttonFocusIndex = 1;
                } else if ( event.event.srcElement === event.$item.$buttons[2] ) {
                    event.$item.buttonFocusIndex = 2;
                } else {
                    event.$item.buttonFocusIndex = 0;
                }
            }

            switch ( event.$item.buttonFocusIndex ) {
                case 0:
                    window.pmlAuthError.className = '';
                    if ( typeof window.navigator.onLine !== 'undefined' && !window.navigator.onLine ) {
                        window.pmlLabelError.innerText = gettext('No network connection');
                        window.pmlLabelError.className = 'visible';
                    } else {
                        login(event.$item.data, function ( error, response ) {
                            if ( error ) {
                                if ( error === 'onerror 0 : ' ) {
                                    window.pmlLabelError.innerText = gettext('Portal address entered incorrectly');
                                    window.pmlLabelError.className = 'visible addressError';
                                    inputServerAddress.focus();
                                    inputServerAddress.$node.classList.add('error');
                                } else if ( response && response.error_description ) {
                                    window.pmlAuthError.innerText = (response.error_description);
                                } else {
                                    window.pmlAuthError.innerText = gettext('Error');
                                }

                                if ( event.$item === self.$body.firstChild ) {
                                    window.pmlAuthError.className = 'visible';
                                } else if ( event.$item === self.$body.lastChild ) {
                                    window.pmlAuthError.className = 'visible third';
                                } else {
                                    window.pmlAuthError.className = 'visible second';
                                }
                            } else {
                                accountManager.get().indexOf(event.$item.data);
                                accountManager.update(event.$item.data);
                            }
                        });
                    }
                    break;

                case 1:
                    console.log('edit');
                    setTimeout(function () {
                        window.pmlAccountsContainer.className = 'hidden';
                        window.pmlLabelError.className = '';
                        window.pmlFormContainer.className = '';
                        inputLogin.focus();
                        initCreateForm(event.$item.data, true);
                    });
                    break;

                case 2:
                    console.log('remove');
                    focusIndex = this.$focusItem.index;
                    viewIndex = this.viewIndex;
                    console.log(focusIndex);
                    this.$focusItem.$buttons[this.$focusItem.buttonFocusIndex].classList.remove('focus');
                    this.$focusItem = this.viewIndex = null;
                    accountManager.remove(event.$item.data);
                    if ( this.size === 1 ) {
                        this.data = [];
                        window.pmlAccountsContainer.className = 'hidden';
                        window.pmlLabelError.className = '';
                        window.pmlFormContainer.className = '';
                        inputLogin.focus();
                    } else if ( this.data.length <= 3 ) {
                        --this.size;
                        this.init({
                            size: this.size,
                            viewIndex: viewIndex,
                            focusIndex: focusIndex,
                            data: accountManager.get()
                        });
                    } else {
                        this.init({
                            size: 3,
                            viewIndex: viewIndex,
                            focusIndex: focusIndex,
                            data: accountManager.get()
                        });
                    }
                    break;
            }
        }
    },
    data: accountsData
});

buttonSubmit = new Button({
    $node: window.pmlButtonSubmit,
    value: gettext('Login')
});

buttonNewAccount = new Button({
    $node: window.pmlAddAccount,
    value: gettext('Add account')
});

buttonScrollUp = new Button({
    $node: window.pmlAccountListButtonScrollUp,
    // className: '',
    events: {
        click: function () {
            if ( accountList.viewIndex > 0 ) {
                accountList.renderView(accountList.viewIndex - 1);
                updateAccountsCounter();
                if ( accountList.viewIndex === 0 ) {
                    accountList.focus();
                    buttonScrollUp.disabled = true;
                    buttonScrollUp.$node.className = 'transparent';
                } else {
                    buttonScrollDown.disabled = false;
                    buttonScrollDown.$node.className = '';
                }
            }
        },
        focus: function () {
            this.$node.className = 'focus';
        },
        blur: function () {
            this.$node.className = '';
        },
        keydown: function ( event ) {
            if ( event.code === keys.ok ) {
                this.emit('click');
            } else if ( event.code === keys.down ) {
                accountList.focus();
            }
        }
    }
});

buttonScrollDown = new Button({
    $node: window.pmlAccountListButtonScrollDown,
    // className: '',
    events: {
        click: function () {
            if ( accountList.data.length > (accountList.viewIndex + accountList.size) ) {
                accountList.renderView(accountList.viewIndex + 1);
                updateAccountsCounter();

                if ( (accountList.viewIndex + accountList.size) === accountList.data.length ) {
                    console.log(buttonScrollDown.$node.className);
                    accountList.focus();
                    buttonScrollDown.disabled = true;
                    buttonScrollDown.$node.className = 'transparent';
                }

                buttonScrollUp.disabled = false;
                buttonScrollUp.$node.className = '';
            }
        },
        focus: function () {
            this.$node.className = 'focus';
        },
        blur: function () {
            this.$node.className = '';
        },
        keydown: function ( event ) {
            if ( event.code === keys.ok ) {
                this.emit('click');
            } else if ( event.code === keys.up ) {
                accountList.focus();
            } else if ( event.code === keys.down ) {
                buttonNewAccount.focus();
            }
        }
    }
});

if ( accountList.data.length > 3 ) {
    buttonScrollDown.disabled = false;
    buttonScrollDown.$node.className = '';
} else {
    buttonScrollDown.disabled = true;
    buttonScrollDown.$node.className = 'transparent';
}

