buttonSubmit.addListener('click', function () {
    var submitInterval;

    if ( buttonSubmit.locked ) {
        return;
    }

    buttonSubmit.locked = true;

    if ( typeof window.navigator.onLine !== 'undefined' ) {
        submitInterval = setInterval(function () {
            if ( PLATFORM !== 'MAG' ) {
                if ( !window.navigator.onLine ) {
                    errorNetworkNotification(submitInterval);
                }
            }
        }, 5000);
    }

    // var backend = require('../modules/shared.backend').instance;

    // inputLogin.setValue('samsungdev');
    // inputServerAddress.setValue('http://smart-portal.infomir.com.ua/stalker_portal/');
    // inputPassword.setValue('4aijBoUGK5');
    // if ( DEVELOP ) {
    //     inputLogin.setValue('stv');
    //     inputPassword.setValue('1111');
    //     inputServerAddress.setValue('http://192.168.1.13/stalker_portal/');
    //     inputActivateCode.setValue('3110A46806C7');
    //     if ( PLATFORM === 'SMARTTV' ) {
    //         inputLogin.setValue('smart');
    //         inputPassword.setValue('1111');
    //         inputServerAddress.setValue('http://ad.h.od.ua/stalker_portal/');
    //         inputActivateCode.setValue('114073406EEB');
    //     }
    // }
    // reset style
    window.pmlLabelError.className = '';

    if ( inputLogin.value.length === 0 ) {
        // empty login
        errorNotification(inputLogin);
    } else if ( inputPassword.value.length === 0 ) {
        // empty password
        errorNotification(inputPassword);
    } else if ( inputServerAddress.value.length === 0 ) {
        // empty password
        errorNotification(inputServerAddress);
    } else if ( inputActivateCode.value.length !== 0 && inputActivateCode.value.length !== 12 ) {
        // empty password
        errorNotification(inputActivateCode);
    } else if ( PLATFORM !== 'MAG' && typeof window.navigator.onLine !== 'undefined' && !window.navigator.onLine ) {
        errorNetworkNotification(submitInterval);
    } else {
        login({
            password: inputPassword.value,
            login: inputLogin.value,
            address: inputServerAddress.value,
            activationCode: inputActivateCode.value
        }, function ( error, response ) {
            if ( typeof window.navigator.onLine !== 'undefined' ) {
                clearInterval(submitInterval);
            }

            buttonSubmit.locked = false;

            if ( error ) {
                console.log(JSON.stringify(error));
                console.log(typeof error.indexOf === 'function' && error.indexOf('404') !== -1);
                if ( error === 'onerror 0 : ' || error === '0 : ' ) {
                    window.pmlLabelError.innerText = gettext('Portal address entered incorrectly');
                    window.pmlLabelError.className = 'visible addressError';
                    inputServerAddress.focus();
                    inputServerAddress.$node.classList.add('error');
                } else if ( typeof error.indexOf === 'function' && error.indexOf('404') !== -1 ) {
                    window.pmlLabelError.innerText = gettext('Ministra Player is compatible only with the service of IPTV providers who use Stalker Middleware 5.2 or Ministra TV platform.');
                    window.pmlLabelError.className = 'visible addressError';
                    inputServerAddress.focus();
                    inputServerAddress.$node.classList.add('error');
                } else if ( response && response.error_description ) {
                    errorHandler(response.error_description);
                } else {
                    window.pmlLabelError.innerText = gettext('Error. Please try again.');
                    window.pmlLabelError.className = 'visible';
                }
            } else {
                if ( buttonSubmit.state === 'update' ) {
                    buttonSubmit.state = null;
                    accountManager.update({
                        password: inputPassword.value,
                        login: inputLogin.value,
                        address: inputServerAddress.value,
                        activationCode: inputActivateCode.value
                    }, buttonSubmit.updateId);
                } else {
                    if ( response.activation_code ) {
                        inputActivateCode.setValue(response.activation_code);
                    }

                    accountManager.add({
                        password: inputPassword.value,
                        login: inputLogin.value,
                        address: inputServerAddress.value,
                        activationCode: inputActivateCode.value
                    });
                }

                buttonSubmit.$text.innerHTML = gettext('Add account');
                window.pmlFormContainer.className = 'hidden';

                accountsData = accountManager.get();
                accountList.init({
                    size: accountsData.length < 3 ? accountsData.length : 3,
                    viewIndex: 0,
                    focusIndex: 0,
                    data: accountManager.get()
                });

                window.pmlAccountsContainer.className = '';
                accountList.focus();
            }
        });
    }
});

