'use strict';


var app             = require('mag-app'),

    md5             = require('./md5'),
    xorString       = require('./xor.string'),
    // sharedBackend   = require('./shared.backend').instance,

    parallel = require('cjs-async/parallel'),

    storageKey     = 'applications-storage',

    currentSessions = [],

    userFsStorage = core.storage,

    loader;


function loadTheme ( done ) {
    var globalOptions = core.config.options,
        xhr = new XMLHttpRequest();

    xhr.open('GET', globalOptions.stalkerHost + '/stalker_portal/server/api/launcher_profile.php?language=en&uid=' + app.stalker.session.userId);
    xhr.onload = function () {
        var config = null;

        try {
            config = JSON.parse(this.responseText);
        } catch ( error ) {
            console.log(error);
        }

        if ( config !== null ) {
            if ( config.options.themePath ) {
                config.theme = {
                    path: globalOptions.stalkerHost + '/stalker_portal/server/api/theme_css.php?uid=' +
                    app.stalker.session.userId + '&_=' + Date.now() + '&resolution=',
                    vars: null
                };

                xhr = new XMLHttpRequest();
                xhr.open('GET', config.theme.path + window.screen.height);

                xhr.onload = function () {
                    if ( this.status === 200 ) {
                        parent.extendCorePrototype('theme', config.theme);
                    }

                    done();
                };

                xhr.onerror = xhr.ontimeout = function () {
                    done();
                };

                xhr.send();
            }
        }
    };

    xhr.onerror = xhr.ontimeout = function () {
        done();
    };

    xhr.send();
}


